/* PROFILE */
.profile .menu-title {
  font-size: 18px;
  margin-bottom: 0;
  color: #333;
}

.profile .fas {
  color: #333;
}

.profile .menu-subtitle {
  margin-bottom: 0;
  font-weight: 300;
  color: #999;
  font-size: 14px;
}

.profile .menu-subtitle-small {
  margin-bottom: 0;
  font-weight: 300;
  color: #7f7f7f;
  font-size: 12px;
}

.input-feedback {
  width: 100%;
}

.react-switch {
  vertical-align: middle;
  margin-left: 4px;
}

.toggle-icon-times,
.toggle-icon-check{
    position: relative;
    margin: 0;
    right: 0;
    font-size: 16px;
    color: #fff!important;
}


