/* MY REQUEST */
.my .modal-backdrop .modal .modal-content {
  min-height: 50vh !important;
}

.my-request {
  margin: 5px 0 0 0 !important;
  background: #fff;
  margin-bottom: 0.5em !important;
  border-radius: 5px;
  -webkit-box-shadow: 3px 3px 5px 2px rgba(50, 50, 50, 0.3);
  box-shadow: 3px 3px 5px 2px rgba(50, 50, 50, 0.3);
  padding: 15px;
}

.my-request .chevron {
  color: #e5e5e5;
  margin: auto 0;
}

.my-request .items h4 {
  font-size: 14px;
  font-weight: bold;
}
.my-request .items p {
  margin-bottom: 0;
  -ms-flex-direction: row !important;
      flex-direction: row !important;
}

.my-request .items .review-purchase {
  color: #b9b957;
  font-size: 12px;
}

.my-request .items .info-recusa {
  color: #ef473a !important;
  font-size: 12px;
}

.my-request .items .approved-purchase {
    font-size: 12px;
    color: #28a54c;
}

#my-request,
#my-request-card{
    height: 100vh;
    min-height: 100vh;
    background: rgba(0,0,0,0.75);
}

#my-request .modal-content{
    height: 100vh;
    border: none;
    background: transparent;
}

#my-request .modal-content .modal-title{
    width: 100%;
    text-align: center;
}

#my-request .modal-header{
    border: none;
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    background: #fff;
}

#my-request-card .modal-header{
  color: #fff;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

#my-request-card .modal-header,
#my-request-card .modal-content{
    background: transparent;
    border: none;
}

#my-request .modal-body{
    bottom: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    border: none;
    background: #fff;
    border-radius: 0px 0px 5px 7px;
}

#my-request .modal-body .digitable-line{
    position: absolute;
    bottom: 35px;
}

#my-request .bank-sink-title {
    color: #333;
    margin-bottom: 0;
}

#my-request .bank-sink-subtitle {
    color: #666;
    margin-top: 0;
}

#my-request .modal-body .digitable-line .bank-sink-info {
    color: #666;
    font-weight: 700;
    font-size: 16px;
    margin-top: 100px;
}

#my-request .modal-body .digitable-line .line{
    word-break: break-all;
}

#my-request .modal-footer{
    bottom: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    border: none;
    background-color: transparent;
    padding-right: 0px;
    padding-left: 0px;
}

#my-request .close,
#my-request-card .close{
    border: thin solid;
    border-radius: 50px;
    padding: 0px 0px 6px 0;
    margin: -8px;
    width: 40px;
    height: 40px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    position: absolute;
    right: 20px;
    font-size: 30px;
    font-weight: bold;
    background: #007bff;
    color: #fff;
    opacity: 1;
}


