/* FAQ */
.panel:not(:last-child) {
    margin: 0;
}

.panel-body p{
    padding: 0 10px 10px;
    text-align: justify;
    font-size: 14px;
    margin: 0;
}

.panel-group .panel {
  border-radius: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
  border-color: #eeeeee;
}

.panel-default > .panel-heading {
  padding: 0;
  border-radius: 0;
  color: #212121;
  background-color: #fafafa;
  border-color: #eeeeee;
}

.panel-title {
  font-size: 14px;
  margin: 0;
}

.panel-title > a {
  display: block;
  padding: 15px;
  text-decoration: none;
  color: #007bff;
}

.more-less {
  float: right;
  color: #007bff;
}

.panel-default > .panel-heading + .panel-collapse > .panel-body {
  border-top-color: #eeeeee;
  background: #fff;
}



