/* MENU */
.row-profile {
  /* box-shadow: 3px 3px 5px 2px rgba(50, 50, 50, 0.3); */
  background: #fff;
  margin-bottom: 30px;
  color: #333;
  border-top: thin solid #e5e5e5;
  border-bottom: thin solid #e5e5e5;
}

.row-profile .item {
  padding: 25px 0;
}

.row-profile .item h5 {
  margin-bottom: 0;
}

.row-profile .item h6 {
  color: #cdcdcd;
  font-size: 12px;
}

.row-profile .avatar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  font-size: 28px;
}

.row-profile .avatar i {
  background-color: #c71585;
  color: #fff;
  padding: 5px 0px 0px 3px;
  border-radius: 5px;
  width: 30px;
  height: 30px;
}

.row-profile .chevron,
.row-menu .chevron {
  color: #e5e5e5;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
}

.menu-box {
  background: #fff;
  margin-bottom: 30px;
  border-top: thin solid #e5e5e5;
  border-bottom: thin solid #e5e5e5;
  margin-left: -15px;
  margin-right: -15px;
}

.row-menu {
  color: #333;
  margin: 0;
}

.row-menu .item .title {
  color: #333;
  font-size: 18px;
  font-weight: 500;
}

.row-menu .item:nth-child(2),
.row-menu .item:nth-child(3) {
  border-bottom: thin solid #e5e5e5;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.no-border {
  border: none !important;
}

.row-menu .item {
  padding: 10px 0;
}

.row-menu .avatar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  font-size: 22px;
}

.row-menu .avatar i {
  color: #fff;
  padding: 5px;
  border-radius: 5px;
}

.commissioner-item{
  /* border: thin solid; */
  margin-left: 15px;
  min-width: 150px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: row;
      flex-direction: row;
}

.commissioner-item .fas{
  padding-right: 5px;
  color: #e5e5e5;
}

.commissioner-item .badge{
  font-size: 10px;
  margin-right: 20px;
}


