.notifications .items{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
}

.notifications .items > h4 > small{
    color: #7f7f7f;
}

.notifications .chevron{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    -ms-flex-align: center;
        align-items: center;
    color: #e5e5e5;
}

.notification-date{
    text-align: right;
    color: #7f7f7f;
    font-style: oblique;
    font-size: 12px;
    font-weight: 700;
}


