#commissioner {
  padding-left: 0;
  padding-right: 0;
}

#commissioner .nav-link.active {
  background-color: #007bff;
  border: none;
  color: rgba(255, 255, 255, 1) !important;
  border-bottom: 2px solid #fff;
}

#commissioner .nav-tabs .nav-link {
  color: rgba(255, 255, 255, 0.65);
}

#commissioner nav {
  margin-top: 37px !important;
  background-color: #007bff;
}

#commissioner .nav-item {
  width: 33%;
}

#commissioner .tab-pane {
  min-height: 70vh;
  padding: 15px;
}

#commissioner .row-items,
#my-request-card .row-items {
  color: #333;
  margin: 0;
  padding: 0 15px;
}

#commissioner .row-items .item .title,
#my-request-card .row-items .item .title {
  color: #333;
  font-size: 18px;
  font-weight: 500;
}

#commissioner .row-items .col-title,
#my-request-card .row-items .col-title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  font-weight: normal;
  color: #666;
}

#commissioner .row-items .chevron,
#my-request-card .row-items .chevron {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  color: #e5e5e5;
}

.no-border {
  border: none !important;
}

#commissioner .row-items .item,
#my-request-card .row-items .item {
  padding: 10px 0;
}

#commissioner .row-items .col-invitation,
#my-request-card .row-items .col-invitation {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

#commissioner .row-items .col-invitation h2,
#my-request-card .row-items .col-invitation h2 {
  font-size: 32px;
  color: #333;
  margin-bottom: 0;
}

#commissioner .row-items .col-invitation h5,
#my-request-card .row-items .col-invitation h5 {
  color: #999;
  font-size: 16px;
}

#commissioner .row-items .invitation,
#my-request-card .row-items .invitation {
  padding: 30px 0;
}

#commissioner .row-items .avatar,
#my-request-card .row-items .avatar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-line-pack: center;
      align-content: center;
  font-size: 22px;
}

#commissioner .row-items .avatar i,
#my-request-card .row-items .avatar i {
  color: #fff;
  padding: 5px;
  border-radius: 5px;
  width: 35px;
  height: 35px;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

#commissioner .price-box h3,
#commissioner .price-box h5,
#commissioner .price-box h6 {
  margin: 0;
  text-align: center;
}

#commissioner .price-box h3 {
  font-size: 46px;
  font-weight: bold;
  color: #666;
}

#commissioner .price-box h3 > small {
  color: #666;
  vertical-align: super;
  font-weight: bold;
  font-size: 16px;
}

#commissioner .price-box h5 {
  color: #666;
  font-size: 16px;
  margin-top: 10px;
}

#commissioner .price-box h6 {
  font-size: 11px;
  margin-bottom: 10px;
  color: #7f7f7f !important;
  font-weight: normal;
}

.input-price {
  border: none;
  padding: 0;
  font-size: 18px;
  font-weight: 500;
  margin: 0;
  line-height: 1.2;
}

#my-request-card .fa-check,
#my-request-card .fa-times {
  position: absolute;
  margin-top: 0px;
  right: -55px;
  font-size: 25px;
}

.price-tax {
  font-size: 12px;
  color: #999;
}

.modal-order-sold .modal-content {
  max-height: calc(100vh - 100px);
}

.badge-invitations {
  position: absolute;
  top: 65px;
  z-index: 99;
  right: 142px;
  border-radius: 50px;
  padding: 3px;
  background-color: #11c1f3;
}

.badge-courtesies {
  position: absolute;
  top: 65px;
  z-index: 99;
  right: 15px;
  border-radius: 50px;
  padding: 3px;
  background-color: #33cd5f;;
}



