/* EVENT */
.event {
  padding: 15px 0 0 0;
}

.event .card-img-top {
  margin-top: 37px;
  border-radius: 0;
}

.event-place,
.event-map {
  margin: 0;
  color: #666;
  font-size: 16px;
  font-weight: bold;
}

.event-map {
  font-size: 12px;
  font-weight: 400;
  margin-top: 15px;
}

.event-map i {
  margin-right: 5px;
}

.event-title {
  font-size: 20px;
  font-weight: bold !important;
  white-space: nowrap !important;
  overflow: initial !important;
  margin: 0;
  margin-top: 15px;
  text-align: left;
}

.event-date {
  font-size: 20px;
  color: #666;
}

.event-date-col {
  -ms-flex-pack: end !important;
      justify-content: flex-end !important;
  padding: 0;
  padding-right: 5px;
}

.ticket-name {
  font-size: 18px !important;
  padding-top: 0px;
  color: #000 !important;
  font-weight: bold;
  border-bottom: 2px solid #000;
}

.lot-name {
  color: #000 !important;
  font-size: 14px;
  margin-bottom: 0;
}

.lot-price {
  color: #666 !important;
  margin: 0 0 4px;
  font-size: 10px;
}

.lot-big-price {
  color: #666;
  text-align: right;
  font-weight: 400;
}

.text-default {
  color: #666 !important;
}

.cart-footer {
  position: fixed;
  bottom: 0;
  background: #e4e0e0;
  width: 100%;
  height: 82px;
  margin: 0;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}

.cart-footer .col-price {
  background: #fff;
  margin: 5px;
  border-radius: 5px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}

.cart-footer .col-purchase {
  background: #fff;
  color: #fff;
  /* background-image: url("../../images/button-purchase.png");
  background-size: contain; */
  background-color: #007bff;
  margin: 5px;
  border-radius: 5px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 22px !important;
  font-weight: 700 !important;
}

.cart-footer .col-price h6 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.2;
  text-align: center;
  color: #666 !important;
}
.cart-footer .col-price h5 {
  margin-top: 0;
  font-size: 26px;
  margin-bottom: 0;
  text-align: center;
}

.height-0 {
  height: 0px;
}



