/* CHOOSE QUANTITY */
.choose-quantity{
    border: 2px solid #dddddd;
    border-radius: 3px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding: 5px;
}
.minus,
.plus{
    height: 30px;
    width: 30px;
    padding: 5px;
    margin: 0;
    font-size: 16px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    float: left;
    border-radius: 50%;
    color: green;
    background: transparent;
    -webkit-box-shadow: none;
            box-shadow: none;
}

.minus:focus,
.plus:focus{
    outline: none;
}

.minus:active,
.plus:active{
    margin-top: 1px;
}

.qtd{
    width: 30px;
    height: 30px;
    font-size: 28px;
    color: #666!important;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.qtd:focus{
    -webkit-box-shadow: none;
            box-shadow: none;
}

.space-button{
    height: 30px;
    width: 30px;
    padding: 5px;
    margin: 0;
    float: left;
}


