/* MY TICKETS */

.ticket-image{
    max-width: 80px;
    max-height: 80px;
    border-radius: 5px 0 0 5px;
}

.ticket-title{
    font-size: 20px;
}

#my-ticket{
    height: 100vh;
    min-height: 100vh;
    background: rgba(0,0,0,0.75);
}

#my-ticket .modal-content{
    height: 100vh;
    border: none;
    background: transparent;
    margin: 50px 0 0 0px;
    max-height: calc(100vh - 60px);
}

#my-ticket .modal-content .modal-title{
    width: 100%;
    text-align: center;
}

#my-ticket .modal-header{
    border: none;
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    background: #fff;
}

#my-ticket .modal-body{
    bottom: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    border: none;
    background: #fff;
    border-radius: 0px 0px 5px 7px;
}

#my-ticket .modal-body .digitable-line{
    position: absolute;
    bottom: 35px;
}

#my-ticket .bank-sink-title {
    color: #333;
    margin-bottom: 0;
}

#my-ticket .bank-sink-subtitle {
    color: #666;
    margin-top: 0;
}

#my-ticket .modal-body .digitable-line .bank-sink-info {
    color: #666;
    font-weight: 700;
    font-size: 16px;
    margin-top: 100px;
}

#my-ticket .modal-body .digitable-line .line{
    word-break: break-all;
}

#my-ticket .modal-footer{
    bottom: 0;
    display: block;
    border: none;
    background-color: #fff;
    padding-right: 0px;
    padding-left: 0px;
    border-radius: 5px;
    margin-top: 5px;
    padding: 15px;
}

#my-ticket .close{
    border: thin solid;
    border-radius: 50px;
    padding: 0px 0px 6px 0;
    margin: -8px;
    width: 40px;
    height: 40px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    position: absolute;
    right: 20px;
    font-size: 30px;
    font-weight: bold;
    background: #007bff;
    color: #fff;
    opacity: 1;
}

#my-ticket .identity-document{
    letter-spacing: 3px;
    font-weight: bold;
    text-align: center;
}

#my-ticket .user-name{
    font-weight: 400;
    text-align: center;
}

#my-ticket .date{
    font-weight: 400;
    color: #666;
}

#my-ticket .ticket-name{
    border: none;
    text-align: center;
    font-size: 28px!important;
}

#my-ticket .attention{
    border-bottom: thin solid;
    text-align: left;
    font-size: 1.2em;
    font-weight: bold;
    padding-bottom: 10px;
}


