/* CONTACT */
.contact-row-title{
    width: 100%;
    color: #fff;
    text-align: center;
}

.contact-row-title h4{
    margin: 5px 0px;
}

.contact .item{
    width: 100%;
    padding: 5px;
    margin-left: 5px;
}

.contact .item-icon-left .icon {
    font-size: 20px;
    left: 0;
    position: relative;
    float: left;
    color: #444!important;
    line-height: 1.5;
    margin-right: 5px;
}

.box-social-icons {
    width: 100%;
    text-align: center;
}

.box-social-icons .fa,
.box-social-icons .fab,
.box-social-icons .fas,
.box-social-icons .far {
    color: #444!important;
    font-size: 40px;
    padding: 10px 5px;
}


