.support > div:first-child,
.support > div:nth-child(3){
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.support > div:first-child > i{
    font-size: 38px;
    color: #5f5f5f;
}

.support > div:nth-child(2) > h4{
    color: #5f5f5f;
}

.support > div:nth-child(3) > i{
    font-size: 38px;
    color: #34af23;
}

.row-whatsapp{
    margin-left: -15px!important;
    margin-right: -15px!important;
    margin-bottom: 10px;
}

.row-whatsapp .col > a{
    padding: 15px 0px;
    font-size: 18px;
    font-weight: bold;
    background-color: #33cd5f;
}


