/* REGISTER */
.text-alert {
    border-radius: 3px;
    border: thin solid red;
    color: red!important;
    padding: 5px;
    text-align: center;
    font-weight: bold;
}

.text-alert strong{
    color: red;
}

label{
    margin-bottom: 0;
    margin-top: .5rem;
}

.custom-checkbox{
    border: thin solid #ccc;
    margin-top: 15px;
    padding: 0px 0px 10px 1.9rem;
    border-radius: 5px;
}

.fa-check,
.fa-times{
    position: absolute;
    margin-top: -33px;
    right: 30px;
    font-size: 25px;
}

.fa-check{
    color: limegreen;
}

.fa-times{
    color: red;
}

.register-modal-content{
    min-height: 100vh;
}


